.page-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start; /* Adjust if necessary */
    height: 100%; /* Adjust height */
    margin: auto;
    padding: 0;
    overflow: auto;
    text-align: left;
    width: 75%; /* Adjust width if necessary */
    margin-top: 6vh;
}

.page-text {
    font-size: 1.5em;
    margin-bottom: 0;
    padding-bottom: 0.5vh;
    text-align: left;
}

.page-text2 {
    font-size: 1.2em;
    margin-bottom: 0;
    padding-bottom: 0.5vh;
    text-align: left;
}

.page-header {
    font-size: 1.6em;
    margin-bottom: 0;
    padding-bottom: 0.5vh;
    text-align: left;
    text-decoration: underline;
    font-style: italic;
}